import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Games from '@/components/Games'
import Apps from '@/components/Apps'
import Contact from '@/components/Contact'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Games',
      name: 'Games',
      component: Games
    },
    {
      path: '/Apps',
      name: 'Apps',
      component: Apps
    },
    {
      path: '/Contact',
      name: 'Contact',
      component: Contact
    }
  ]
})



